let _this
export default {
	data() {
		return {
			form:{
				creditToken: '',	//授信令牌
				userCode: ''		,//节点码
				leftRight: ''
			},
			items: [{
			                    value: '1',
			                    name: '熵增',
								checked: 'true'
			                },
			                {
			                    value: '2',
			                    name: '熵減' 
			                }
			               
			            ],
			current: 0			
		};
	},
	onLoad:function(){
		_this = this
	},
	methods:{
		radioChange: function(evt) {
		            for (let i = 0; i < this.items.length; i++) {
		                if (this.items[i].value === evt.target.value) {
		                    this.current = i;
		                    break;
		                }
		            }
					console.log(this.current)
		},
		async submit(){
			this.form.leftRight = parseInt(this.items[this.current].value) 
			
			const res = await this.$http.get('/user/set_token',{data: this.form})
			console.log(res)
			var pages = getCurrentPages();
			var currPage = pages[pages.length - 1]; //当前页面
			var prevPage = pages[pages.length - 2]; //上一个页面
			//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
			prevPage.$vm.isActive = true
			// prevPage.setData({
			//    isActive:true
			// })
			uni.showToast({
				title: '激活成功',
				icon: 'none'
			});
			
			uni.navigateBack({
				delta: -1
			});
			 
		},
		scanCodeToken:function(){
			
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
					_this.form.creditToken = res.result
			    }
			});
		},
		scanCodeNode: function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
					_this.form.userCode = res.result
			    }
			});
		}
	}
}
